<template>
  <div class="headLine">
    <div class="qihuotoutiao">
      <div class="toutiaoTime">
        {{nowDate}}
      </div>
    </div>
    <!--  -->
    <div class="lists" v-for="(item,index) in datas" :key="item.id">
      <div class="listHeader">
        <span class="BigNum" ><span v-if="index != 9">0</span>{{index+1}}.</span>
        <span class="listHeaderContent"><span class="bold">  {{item.create_at.slice(11,16)}}</span></span>
      </div>
      <div class="listContent">
        <div class="redBack">{{item.title}}</div>
        <p class="articleTitle">
          <span>{{item.introduction}}</span>
          <span class="chaKan" @click="chaKan(item.content,item.create_at,item.title)">>></span>
        </p>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'headLine',
    data() {
      return {
        nowDate:'',
        datas:'',
      }
    },
    components: {},
    computed: {},
    beforeMount() {},
    mounted() {
      var that = this;
      this.today();
      this.axios.get(host + '/qi/page.news/news?type=13' )
      .then(function (response) {
        console.log(response)
        that.datas = response.data.msg
      })
      .catch(function (error) {
      })
    },
    methods: {
      chaKan(content,time,title) {
        this.$router.push({
          name: 'headLineContent',
          query: {
            content: content,
            time:time,
            title:title,
          }
        })
      },
      today(){
        var that = this;
        var date = new Date();
        var nowMonth = date.getMonth() + 1;
        var strDate = date.getDate();
        var day = date.getDay();
        var dayChinese = ''
        switch (day) {
          case 1:
            dayChinese = ''
            break;
            case 2:
            dayChinese = ''
            break;
            case 3:
            dayChinese = ''
            break;
            case 4:
            dayChinese = ''
            break;
            case 5:
            dayChinese = ''
            break;
            case 6:
            dayChinese = ''
            break;
            case 7:
            dayChinese = ''
            break;
        }
        if (nowMonth >= 1 && nowMonth <= 9) {
          nowMonth = "0" + nowMonth;
        }
        if (strDate >= 0 && strDate <= 9) {
          strDate = "0" + strDate;
        }
        var nowDate = date.getFullYear() + '' + nowMonth + '' + strDate +' '+''+dayChinese;
        that.nowDate = nowDate;
      }
    },
    watch: {},
  }

</script>
<style scoped>
  .chaKan {
    color: #fb004d;
    margin-left: .1rem;
    cursor: pointer;
  }

  .articleTitle {
    font-size: .3rem;
    text-align: left;
    margin-bottom: .3rem;
  }

  .articleTitle span {
    display: inline;
  }

  .listContent {
    padding-left: .5rem;
    padding-right: .5rem;
  }

  .redBack {
    background: #fb004d;
    color: white;
    font-size: .34rem;
    /* height: .6rem; */
    line-height: .6rem;
    text-align: left;
    width: fit-content;
    padding: 0 .2rem;
    margin-top: .2rem;
    margin-bottom: .3rem;
  }

  .lists {
    border: 1px solid rgb(229, 229, 229);
    position: relative;
    margin-bottom: .4rem;
  }

  .listHeader {
    /* width: 4rem; */
    display: flex;
    align-items: baseline;
  }

  .bold {
    font-weight: 600;
        text-align: left;
        max-width: 5.25rem;
  }

  .listHeaderContent {
    color: black;
    font-size: .3rem;

  }

  .BigNum {
    color: #fb004d;
    font-size: .6rem;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    margin-left: -1px;
    margin-top: -1px;
    background: white;
    margin-right: .1rem;
width: 0.9rem;
  }

  .headLine {
    padding-left: .37rem;
    padding-right: .37rem;
    font-family: 'PingFangMedium';
        background: white;
  }

  .qihuotoutiao {
    background: url('../assets/img/qihuotoutiaologo.png') no-repeat;
    background-size: contain;
    width: 3.84rem;
    height: 1.67rem;
    margin-top: .39rem;
    margin-bottom: .54rem;
    margin-left: -.37rem;
  }

  .toutiaoTime {
    font-size: .28rem;
    color: white;
    width: 3.2rem;
    height: .54rem;
    line-height: .54rem;
    letter-spacing: -1px;
    margin-left: .37rem;
  }

</style>
<style>
  #app {
    background: white;
  }

</style>



