<template>
  <!--mescroll-->
  <div class="p1">
    <div class="content">
      <div class="stockContentOK" v-if="allowUser">
        <ul class="stockSection">
          <li class="stockList" v-for="(item,index) in arrayClassOne" :key="index">
            <div>
              <div class="stocktime">
                <div class="leftFlex">
                  <div class="stocktimeicon"></div>
                  <div class="heYueMingCheng">
                    : {{item.stock}}({{item.stock_name}})
                  </div>
                </div>
                <div class="stockTimes">
                  {{item.create_at}}
                </div>
              </div>
              <div class="stockListContain">
                <div class="stockSign">
                  {{item.sign}}
                </div>
                <div class="stockLeft">
                  <div class="stockWidthL stockTitle stockListDetailNum">
                    <span class="stockSpan1">:</span>
                    <span class="stockSpan2 stockSpan2width1">{{item.position}}</span>
                  </div>

                  <div class="stockListDetailNum">

                    <div>
                      <span class="stockSpan1">:</span>
                      <span class="stockSpan2 stockSpan2width2">{{item.buy_price}}</span>
                    </div>

                    <div class="stockWidthL">
                      <span class="stockSpan1">:</span>
                      <span class="stockSpan2 stockSpan2width1" v-if="item.operation_type == 1"></span>
                      <span class="stockSpan2 stockSpan2width1" v-if="item.operation_type == 2"></span>
                      <span class="stockSpan2 stockSpan2width1" v-if="item.operation_type == 3"></span>
                      <span class="stockSpan2 stockSpan2width1" v-if="item.operation_type == 4"></span>
                      <span class="stockSpan2 stockSpan2width1" v-if="item.operation_type == 5"></span>
                    </div>
                  </div>
                  <div class="stockListDetailNum">
                    <div>
                      <span class="stockSpan1">: </span>
                      <span class="stockSpan2 stockSpan2width2">{{item.profit}}</span>
                    </div>
                    <div>
                      <span class="stockSpan1">:</span>
                      <span class="stockSpan2 stockSpan2width2">{{item.loss}}</span>
                    </div>
                  </div>
                </div>
                <div class="stockListTipImage">
                  <!--  1: 2:  3:  4: -->
                  <img :src="srcGoing" alt=""
                    v-if="item.operation_type == 1 || item.operation_type == 2 || item.operation_type == 5"
                    @click="linkStockgoing(item.id)">
                  <img :src="srcloss" alt="" v-else-if="item.operation_type == 4" @click="linkStockgoing(item.id)">
                  <img :src="srcwin" alt="" v-else-if="item.operation_type == 3" @click="linkStockgoing(item.id)">
                </div>
              </div>
            </div>

          </li>
        </ul>
        <div class="daoDi" v-show="daoDiShow">
          ~
        </div>
      </div>
      <div class="onlegal" v-else>
         
      </div>

    </div>

  </div>
</template>
<script>
  var pageIndex = 1; //
  var isDaoDi = true;
  var ppbtn = true;
  //


  export default {
    name: 'p1',
    data() {
      return {
        allowUser: true,
        srcGoing: require('@/assets/img/stockgoing.png'),
        srcloss: require('@/assets/img/stockloss.png'),
        srcwin: require('@/assets/img/stockwin.png'),
        arrayClassOne: [],
        arrayClassOneTest: [
          {
          stock_name: "1123",
          position: "2",
          create_at: '10:30',
          buy_price: '1',
          operation_type: "5",
          profit: '2',
          loss: 'name',
          sign: '111'
        }, {
          stock_name: "1123",
          position: "2",
          create_at: '10:30',
          buy_price: '1',
          operation_type: "3",
          profit: '2',
          loss: 'name',
          sign: '111'
        }
        ],
        daoDiShow: false,
      }

    },

    computed: {
      pid: function () {
        return this.$store.state.pid
      }
    },
    watch: {
      pid(val) {
        isDaoDi = true;

        this.daoDiShow = false;
        this.loadList(val);
      }
    },
    activated() {
      window.removeEventListener('scroll', this.onScroll);
      isDaoDi = true;
      this.arrayClassOne = []
      //console.log('activated')
      this.loadList(this.pid);
      this.daoDiShow = false;
    },
    methods: {
      //  
      linkStockgoing(id) {
        isDaoDi = true;
        pageIndex = 1;
        this.arrayClassOne = [];
        //console.log(id);
        this.$router.push({
          name: 'stockHistory',
          query: {
            pid: id
          }
        })
        location.reload();
      },
      onScroll() {
        if (this.$route.name == 'p1') {
          var that = this;
          //console.log(111)
          let innerHeight = document.querySelector('#mainPage').clientHeight;
          //
          let outerHeight = document.documentElement.clientHeight;
          //
          let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
          //scrollTop0innerHeight < (outerHeight + scrollTop)
          //console.log(innerHeight + " " + outerHeight + " " + scrollTop);
          if (innerHeight <= (outerHeight + scrollTop)) {

            //console.log('')
            //
            // if (isDaoDi) {
            pageIndex++; //
            var that = this;
            var form = new FormData();
            //console.log('', pageIndex)
            form.append("model", "get_strategy");
            form.append("page", pageIndex);
            form.append("offset", 5);
            form.append("product_id", this.pid);
            $.ajax({
              type: "POST",
              url: host + "/qi/page/index/index",
              data: form,
              mimeType: "text/html",
              processData: false,
              contentType: false,
              dataType: 'json',
              success: function (res) {
                console.log(res)
                res = res.data
                if (res.length == 0) {
                  isDaoDi = false;
                  that.daoDiShow = true;
                } else {
                  isDaoDi = true;
                  if (res.length > 0) {
                    for (let i = 0; i < res.length; i++) {
                      that.arrayClassOne.push(res[i])
                    }

                  }
                  //offset
                  if (res.length < 5) {
                    that.daoDiShow = true;
                  }
                }
                //console.log('that.arrayClassOne',that.arrayClassOne)
              }
            });
            this.items += 10;


          }
        }
      },
      loadList(val) {

        if (val !== '') {
          this.arrayClassOne = [];
          pageIndex = 1;
          // var loading = this.$loading({
          //   lock: true,
          //   text: '',
          //   spinner: 'el-icon-loading',
          //   background: 'rgba(255, 255, 2550.3)'
          // });
          //console.log("loadmore");
          var that = this;
          var form = new FormData();
          form.append("model", "get_strategy");
          form.append("page", pageIndex);
          form.append("offset", 5);
          form.append("product_id", val);

          $.ajax({
            type: "POST",
            url: host + "/qi/page/index/index",
            data: form,
            mimeType: "text/html",
            processData: false,
            contentType: false,
            dataType: 'json',
            success: function (res) {
              console.log(res)
              res = res.data
              that.arrayClassOne = res

              window.addEventListener('scroll', that.onScroll);
            }
          });


        }
      }
    }
  }

</script>
<style scoped>
  .stockTimes {
    color: #747474;
    font-size: .24rem;
  }


  .stockTitle {
    color: #747474;
    text-align: left;
  }

  .p1 {
    background-color: #f5f5f4;
    /* background-color: red; */

  }

  .content {
    font-size: .2rem;

  }

  .stockContentOK {
    padding: 0.2rem 0rem 0rem 0rem;
  }

  .stockList {
    border-radius: .06rem;
    background-color: #fff;
    margin-bottom: 0.12rem;

  }

  .stocktime {
    overflow: hidden;
    height: .78rem;
    line-height: .78rem;
    border-bottom: 1px solid #f5f5f4;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: .3rem;
    color: #3a393a;
    padding: 0 .19rem;
    line-height: .78rem;
    /* font-weight: bold; */
  }

  .stocktime {
    text-align: left
  }

  .stocktimeicon {
    width: .1rem;
    height: .28rem;
    background-color: #fb5646;
    border-radius: .05rem;
    margin-right: .1rem;
  }

  .stockListContain {
    overflow: hidden;
    padding: .4rem .35rem;
    position: relative;
    left: 0;
    top: 0;
    z-index: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .stockListDetail {
    float: left;
    width: 4.9rem;
  }

  .stockListDetailNum {
    color: #747474;
    line-height: .5rem;
    overflow: hidden;
  }

  .stockListDetailNum li {
    float: left;
    overflow: hidden;
    width: 50%;
  }

  .stockListDetailNum li span {
    float: left;
    font-size: .24rem;
    line-height: .44rem;
    height: .44rem;
    overflow: hidden;
    text-align: left;
  }

  .stockListDetail .stockSpan1 {
    color: #747474;
  }

  .stockListDetail .stockSpan2 {
    /* font-weight: bold; */
  }

  .stockSpan2width1 {
    /* width: 1.1rem; */
  }


  .stockListDetailReason {
    padding-top: .1rem;
    font-size: .24rem;
    line-height: .38rem;
    text-align: left;
    color: #3a393a;
  }

  .stockListTipImage {
    width: 1.61rem;
    height: 1.61rem;
    display: flex;
    align-items: center;
  }

  .stockListTipImage img {
    width: 100%;
    /* height: 100%; */
    /* position: absolute;
    top: 50%;
    right: .36rem;
    z-index: 0;
    width: 1.61rem;
    margin: auto;
    cursor: pointer;

    transform: translateY(-50%); */
  }

  .onlegal {
    font-size: 0.2rem;
    color: red;
  }

  .stockSign {
    height: .39rem;
    /* width: fit-content; */
    line-height: .39rem;
    color: #909090;
    font-size: .22rem;
    border-top-left-radius: 10px;
    background: #dfdfdf;
    position: absolute;
    bottom: 0;
    right: 0;
    padding-left: .08rem;
    padding-right: .08rem;
  }

  .stockListDetailNum div {
    float: left;
  }

  .stockListDetailNum div:first-child {
    margin-right: .15rem;
  }

  .stockLeft {
    width: 5.3rem;
    font-size: 0.26rem;
  }

  .heYueMingCheng {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .leftFlex {
    display: flex;
    align-items: center;
    width: 4.4rem;
  }

</style>



