<template>
  <div>
    <img src="../assets/img/taolibanner.png" alt="">
    <div class="bar">
      <div class="bars" :class="active[0]"  @click="addActive(0)">
        
      </div>
      <div class="bars" :class="active[1]" @click="addActive(1)">
        
      </div>
      <div class="bars" :class="active[2]" @click="addActive(2)">
        
      </div>
    </div>
    <div class="content">
            <div class="lists" v-for="item in data" :key="item.id" @click="toMore(item.content,item.create_at,item.title)">
        <div class="title">
          <span class="icon" :class="color1"><img src="../assets/img/dujiaicon.png" alt=""><span class="iconInside" :class="color2">{{iconName}}</span></span>
         {{item.title}}
        </div>
        <div class="listsContent" v-html='item.introduction'>
        </div>
        <div class="line"></div>
        <div class="times">
          <img src="../assets/img/riqiicon.png" alt="">
          <span>{{item.create_at.split(' ')[0].replace(new RegExp( '-' , "g" ),'.')}}</span>
          <img src="../assets/img/timeicon.png" alt="">
          <span>{{item.create_at.split(' ')[1].slice(0,5)}}</span>
        </div>
        <div class="grey"></div>
      </div>
    </div>
            <div class="TheEnd" :class="show">
        ~
      </div>
  </div>
</template>
<script>
  export default {
    name: 'TaoLiDaShi',
    data() {
      return {
        active: ['active', '', ''],
        iconName:'',
        iconNames: ['','',''],
        color1: 'red1',
        color2: 'red2',
        arr1: ['red1','purple1', 'orange1'],
        arr2: ['red2','purple2', 'orange2'],
         /*******  /*******/ 
        data:[],
        // index
        typeNum: 10,
        // 
        pageNumber:1,
        show:'none',
      }
    },
    components: {},
    computed: {},
    beforeMount() {},
    mounted() {
       var that = this;
      let typeSess = Number(sessionStorage.getItem("TaoLiTypeSess"))
      if(sessionStorage.getItem("TaoLiTypeSess")){
        that.addActive(typeSess)
      } else {
        this.getdata(10,1)
      }
      window.addEventListener('scroll', this.onScroll);
      
    },
    methods: {
      addActive(index) {
        console.log(index)
        this.typeNum = index+10;
        sessionStorage.setItem("TaoLiTypeSess", index);
        this.getdata(index+10,1);
        this.active = ['', '', ''];
        this.active[index] = 'active';
        this.iconName = this.iconNames[index]
                this.color1 = this.arr1[index];
        this.color2 = this.arr2[index];
                this.show = 'none';
        this.data = [];
        this.pageNumber = 1;
      },
      // 
      getdata(type,pageNum){
         var that = this;
         console.log(type,pageNum)
        this.axios.get(host + '/qi/page.news/news?type='+type,{
           params: {
             page:pageNum,
             size:10
           }
         })
        .then(function (response) {
          var res = response.data.msg
          if(response.data.msg == "") {
            $(".tanChuangBack").show();
            $(".tanchuangExit").click(function () {
              $(".tanChuangBack").hide();
            })
          }
          console.log(res)
          if(res.length>0){
            for (let i = 0; i < res.length; i++) {
             that.data.push(res[i])
          }
          } else {
            that.show = 'block'
          }
          
        })
        .catch(function (error) {
        })
      },
      toMore(content,time,title){
        console.log(content,time,title);
        this.$router.push({
          name: 'headLineContent',
          query: {
            content: content,
            time:time,
            title:title,
          }
        })
      },
      onScroll() {
          var that = this;
          //
          let innerHeight = document.querySelector('#app').clientHeight;
          //
          let outerHeight = document.documentElement.clientHeight;
          //
          let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
          //scrollTop0innerHeight < (outerHeight + scrollTop)
          if (innerHeight <= (outerHeight + scrollTop)) {
            //
            console.log("")
            that.pageNumber++;
            that.getdata(that.typeNum,that.pageNumber);
          }
      }
    },
    watch: {},
  }

</script>
<style scoped>
 
  .grey {
    height: .2rem;
    background: #f3f3f3;
    width: 7.5rem;
    margin-left: -.2rem;
  }
.icon img {
    display: inline;
    width: 0.23rem;
    margin: 0 0.05rem;
        margin-bottom: 0.05rem;
}
  .active {
    border-bottom: .04rem solid #fb004d;
    color: black;
    font-weight: 600;
  }

  .bar {
    display: flex;
    font-size: .3rem;
    font-family: 'PingFangMedium';
    color: #606060;
    background: white;
    border-bottom: 1px solid #e5e5e5;
  }

  .bars {
    width: 33%;
    height: .85rem;
    line-height: .85rem;
  }

  .title {
    padding-top: .2rem;
        /* display: flex; */
    align-items: center;
  }


  .times {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: .6rem;
  }

  .times img {
    width: .2rem;
    height: .2rem;
    margin-bottom: .05rem;
    margin-right: .05rem;
    display: block;
  }

  .times span {
    color: #979797;
    font-size: .22rem;
    margin-right: .1rem;
  }

  .line {
    border-bottom: 1px solid #f7f7f7;
    width: 7.5rem;
    margin-left: -.2rem;
  }

  .chaKan {
    margin: .4rem 0;
  }

  .listsContent p {
    color: #6c6c6c;
    margin: .2rem 0;
  }

  .content {
    color: black;
    padding-left: .2rem;
    padding-right: .2rem;
    background: white;
    text-align: left;
    font-family: 'PingFangMedium';
  }

  .lists {
    font-size: .32rem;
  }


  .icon {
    border-radius: 3px;
    color: white;
    font-size: .12rem;
    padding: 2px;
    font-family: Arial, Helvetica, sans-serif;
    background: #1568ff;
    margin-right: .1rem;
    
    font-size: 0.24rem;
    vertical-align: middle;
    width: fit-content;
  }

  .iconInside {
    border-radius: 3px;
    background: white;
    padding: 0 2px;
    margin-left: .02rem;
  }
 .purple1 {
    background: #8045f8;
  }

  .purple2 {
    color: #8045f8;
  }

  .orange1 {
    background: #ff9f15;
  }

  .orange2 {
    color: #ff9f15;
  }

  .red1 {
    background: #ff158e;
  }

  .red2 {
    color: #ff158e;
  }
</style>



