<template>
  <div class="p4">
    <main>
      <div class="content" v-show="p4show">
        <div class="footHead" v-if="videos != ''"><span></span></div>
        <div class="cards" v-for="(item,index) in videos" :key="index" @click="openVideo(index)">
          <div @click="tongJi(item.id,item.url)"  style="width:100%">
            <div v-if='item.url.indexOf(".mp4") == -1' class="poster">
              <a :href="item.url">
                <div class="time" :class="item.create_at == today?'redColor':'backColor'">{{item.create_at}}</div>
                <img class="videoPoster" :src="item.first_img" />
                <div class="cover"></div>
                <div class="icon">
                  <img src="../assets/img/icon.png" alt="">
                </div>
                <div class="title">
                  {{item.title}}
                </div>
              </a>
            </div>
            <div v-else class="poster"  @click="toMP4(index)" style="display: flex;align-items: center;">
              <div v-show="showPoster !== index">
                  <div class="time" :class="item.create_at == today?'redColor':'backColor'">{{item.create_at}}</div>
                  <img class="videoPoster" :src="item.first_img" />
                  <div class="cover"></div>
                  <div class="icon">
                    <img src="../assets/img/icon.png" alt="">

                  </div>
                  <div class="title">
                    {{item.title}}
                  </div>
              </div>
              <video x5-playsinline="" playsinline="" webkit-playsinline="" v-show="videoShow && showPoster == index"  class="videos" :src="item.url" controls="controls" ></video>  
            </div>
          

          </div>
        </div>

      </div>
      <div class="content" v-show="p4show">
        <div class="footHead" v-if="videos != ''"><span></span></div>
        <div class="cards" v-for="(item,index) in videos1" :key="index" @click="openVideo(index)">


          <div @click="tongJi(item.id,item.url)"  style="width:100%">
            <div v-if='item.url.indexOf(".mp4") == -1'  class="poster">
              <a :href="item.url">
                <div class="time" :class="item.create_at == today?'redColor':'backColor'">{{item.create_at}}</div>
                <img class="videoPoster" :src="item.first_img" />
                <div class="cover"></div>
                <div class="icon">
                  <img src="../assets/img/icon.png" alt="">
                </div>
                <div class="title">
                  {{item.title}}
                </div>
              </a>
            </div>
            <div v-else class="poster"  @click="toMP41(index)" style="display: flex;align-items: center;">
              <div v-show="showPoster1 !== index">
                  <div class="time" :class="item.create_at == today?'redColor':'backColor'">{{item.create_at}}</div>
                  <img class="videoPoster" :src="item.first_img" />
                  <div class="cover"></div>
                  <div class="icon">
                    <img src="../assets/img/icon.png" alt="">

                  </div>
                  <div class="title">
                    {{item.title}}
                  </div>
              </div>
              <video x5-playsinline="" playsinline="" webkit-playsinline="" v-show="videoShow1 && showPoster1 == index"  class="videos" :src="item.url" controls="controls" ></video>  
            </div>
          

          </div>
        </div>

      </div>
      <div class="daoDi" v-show="daoDiShow">
        ~
      </div>
    </main>
  </div>
</template>
<script>
  var pageIndex = 1; //
  var isDaoDi = true;
  export default {
    name: 'p4',
    data() {
      return {
        videos: [],
        videos1: [],
        isActive: '',
        today: '',
        daoDiShow: false,
        p4show: false,
        showPoster:'',
        showPoster1:'',
        videoShow: false,
        videoShow1: false,
        isMp4: '',
      }
    },
    components: {

    },
    created() {
      var date = new Date();
      let month = '';
      let todayDate = ''
      if (date.getMonth() < 9) {
        month = '0' + (date.getMonth() + 1) + ''

      } else {
        month = (date.getMonth() + 1) + ''
      }
      if (date.getDate() < 10) {
        todayDate = '0' + date.getDate() + ''
      } else {
        todayDate = date.getDate() + ''
      }
      this.today = month + todayDate
      console.log(this.today)
    },
    activated() {
      window.removeEventListener('scroll', this.onScroll);
      isDaoDi = true;
      this.arrayClassOne = []
      //console.log('activated')
      this.loadList(this.pid);
      this.daoDiShow = false;
    },
    computed: {
      pid: function () {
        return this.$store.state.pid
      }
    },
    watch: {
      pid(val) {
        isDaoDi = true;

        this.daoDiShow = false;
        this.loadList(val);
      }
    },
    methods: {
      toMP4(index) {
        this.videoShow = true
        this.showPoster = index;
      },
      toMP41(index) {
        this.videoShow1 = true
        this.showPoster1 = index;
      },
      openVideo(index) {
        console.log(index)
        this.isActive = index;
      },
      onScroll() {

        if (this.$route.name == 'p4') {
          var that = this;
          //
          let innerHeight = document.querySelector('#app').clientHeight;
          //
          let outerHeight = document.documentElement.clientHeight;
          //
          let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
          //scrollTop0innerHeight < (outerHeight + scrollTop)
          // console.log(innerHeight + " " + outerHeight + " " + scrollTop);
          if (innerHeight == (outerHeight + scrollTop)) {

            //
            console.log("", isDaoDi)
            that.daoDiShow = true;
            pageIndex++; //
            var that = this;
            var form = new FormData();
            console.log('', pageIndex)
            form.append("model", "get_video");
            form.append("page", pageIndex);
            form.append("offset", 8);
            form.append("product_id", this.pid);
            form.append("type", 1)
            $.ajax({
              type: "POST",
              url: host + "/qi/page/index/index",
              data: form,
              mimeType: "text/html",
              processData: false,
              contentType: false,
              dataType: 'json',
              success: function (res) {
                console.log(res)
                res = res.data
                if (res.length == 0) {
                  isDaoDi = false;
                  that.daoDiShow = true;
                } else {
                  isDaoDi = true;
                  if (res.length > 0) {
                    for (let i = 0; i < res.length; i++) {

                      that.videos1.push(res[i])
                    }
                    console.log(that.videos1)
                  }
                  console.log(res.length)
                  if (res.length < 8) {
                    that.daoDiShow = true;
                  }
                }


              },
              complete: function () {

              }
            });

            this.items += 10;


          }
        }
      },
      loadList(val) {
        if (val !== '') {
          this.videos = [];
          pageIndex = 1;
          // var loading = this.$loading({
          //   lock: true,
          //   text: '',
          //   spinner: 'el-icon-loading',
          //   background: 'rgba(255, 255, 2550.3)'
          // });
          console.log("loadmore");
          var that = this;
          var form = new FormData();
          form.append("model", "get_video");
          form.append("page", 1);
          form.append("offset", 4);
          form.append("product_id", val);
          form.append("type", 0);
          $.ajax({
            type: "POST",
            url: host + "/qi/page/index/index",
            data: form,
            mimeType: "text/html",
            processData: false,
            contentType: false,
            dataType: 'json',
            success: function (res) {

              console.log(res)
              res = res.data
              if (res.length > 0) {
                that.p4show = true;
              }
              that.videos = res

              window.addEventListener('scroll', that.onScroll);
              var form1 = new FormData();
              form1.append("model", "get_video");
              form1.append("page", pageIndex);
              form1.append("offset", 8);
              form1.append("product_id", val);
              form1.append("type", 1);
              $.ajax({
                type: "POST",
                url: host + "/qi/page/index/index",
                data: form1,
                mimeType: "text/html",
                processData: false,
                contentType: false,
                dataType: 'json',
                success: function (res) {

                  console.log("video1", res)
                  res = res.data
                  if (res.length > 0) {
                    that.p4show = true;
                  }
                  that.videos1 = res

                  window.addEventListener('scroll', that.onScroll);

                }
              });
            }
          });

        }

      },
      tongJi(num, url) {
        var that = this;
        var form = new FormData();
        form.append("id", num);
        $.ajax({
          type: "POST",
          url: host + "/qi/page/index/getVideoUrl",
          data: form,
          mimeType: "text/html",
          processData: false,
          contentType: false,
          dataType: 'json',
          success: function (res) {
            console.log('', res)

          },
          complete: function () {
            // location.href = url
          }
        });
      }
      // iframeLink(id) {
      //   console.log(id);
      //   this.$router.push({
      //     name: 'iframePop',
      //     query: {
      //       page: 'p4',
      //       id: id,
      //     }
      //   })
      // },
    },
  }

</script>
<style scoped>
  .videoPoster {
    height: 100%;
    width: 100%;
  }

  .poster {
    height: 100%;
    width: 100%;

  }

  .redColor {
    background: -webkit-linear-gradient(#fe5c4c, #f54938);
    /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(#fe5c4c, #f54938);
    /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(#fe5c4c, #f54938);
    /* Firefox 3.6 - 15 */
    background: linear-gradient(#fe5c4c, #f54938);
    /*  */
  }

  .backColor {
    background: #747373;
  }

  .title {
    background-color: rgba(0, 0, 0, 0.5);
    height: .55rem;
    line-height: .55rem;
    position: absolute;
    left: 0;
    bottom: 0;
    color: white;
    font-size: .22rem;
    text-align: center;
    z-index: 10;
    width: 100%
  }

  .icon {
    z-index: 10;
    height: .59rem;
    width: .59rem;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%)
  }

  .cover {
    background-color: black;
    opacity: 0.5;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
  }

  .cards {
    width: 3.44rem;
    height: 2.63rem;
    display: flex;
    background: black;
    float: left;
    margin-left: .1rem;
    margin-right: .1rem;
    border-radius: 5px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    margin-bottom: .25rem;
  }

  .time {
    position: absolute;
    right: 0;
    top: 0;
    font-size: .2rem;
    width: 1.2rem;
    height: .4rem;
    color: white;
    font-weight: 500;
    text-align: center;
    line-height: .4rem;
    border-bottom-left-radius: 5px;
    z-index: 10;
  }

  video {
    /* max-width: 100%;
    max-height: 100%; */
    width: 100%;
  }

  .content {

    /* padding-bottom: .2rem; */
    background: white;
    padding-left: .15rem;
    overflow: hidden;
  }

  .content:first-child {
    margin-top: .2rem;
  }

  .footHead {
    width: 7rem;
    margin: 0.3rem .1rem;
    height: 1px;
    background-color: #e9e9ec;
    position: relative;
    left: 0;
    top: 0;
    z-index: 0;
    margin-top: .25rem;
  }

  .footHead>span {
    position: absolute;
    left: 50%;
    top: -0.15rem;
    z-index: 0;
    width: fit-content;
    margin-left: -0.56rem;
    height: .3rem;
    line-height: .3rem;
    text-align: center;
    letter-spacing: .06rem;
    font-size: .25rem;
    color: #a0a0a0;
    background-color: #fff;

  }
</style>



