import Vue from 'vue'
import Router from 'vue-router'
import stockHistory from '@/components/stockHistory'
import mainPage from '@/components/mainPage'
import p1 from '@/components/p1'
import p2 from '@/components/p2'
import p3 from '@/components/p3'
import p4 from '@/components/p4'
import iframePop from '@/components/iframePop'
import headLine from '@/components/headLine'
import headLineContent from '@/components/headLineContent'
import tuoshui from '@/components/tuoshui'
import DragonTiger from '@/components/DragonTiger'
import TouZiNeiCan from '@/components/TouZiNeiCan'
import QiHuoXueYuan from '@/components/QiHuoXueYuan'
import TaoLiDaShi from '@/components/TaoLiDaShi'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: mainPage,
      name:  'mainPage',
      children:[
        {
          path: '/',
          name: 'p1',
          component: p1
        },
        {
          path: 'p2',
          name: 'p2',
          component: p2
        },
        {
          path: 'p3',
          name: 'p3',
          component: p3
        },
        {
          path: 'p4',
          name: 'p4',
          component: p4
        }
      ]
    },
    {
      path: '/stockHistory',
      name: 'stockHistory',
      component: stockHistory
    },
    {
      path: '/iframePop',
      name: 'iframePop',
      component: iframePop
    }, {
      path: '/headLine',
      name: 'headLine',
      component: headLine
    }, {
      path: '/headLineContent',
      name: 'headLineContent',
      component: headLineContent
    }, {
      path: '/tuoshui',
      name: 'tuoshui',
      component: tuoshui
    }, {
      path: '/DragonTiger',
      name: 'DragonTiger',
      component: DragonTiger
    }, {
      path: '/TouZiNeiCan',
      name: 'TouZiNeiCan',
      component: TouZiNeiCan
    }, {
      path: '/QiHuoXueYuan',
      name: 'QiHuoXueYuan',
      component: QiHuoXueYuan
    }, {
      path: '/TaoLiDaShi',
      name: 'TaoLiDaShi',
      component: TaoLiDaShi
    }
    
    
    
  ],
  // mode: 'history',
})



