<template>
   <div class="content">
       <div class="title bold">
           {{query.title}}
       </div>
       <div class="times">
           <img src="../assets/img/riqiicon.png" alt="">
           <span>{{query.time.split(' ')[0].replace(new RegExp( '-' , "g" ),'.')}}</span>
           <img src="../assets/img/timeicon.png" alt="">
           <span>{{query.time.split(' ')[1].slice(0,5)}}</span>
       </div>
       <div class="line"></div>
       <div class="fonts" v-html="query.content">
       </div>
       
   </div>
</template>
<script>
export default {
   name: 'headLineContent',
   data() {
       return {
           query:''
       }
   },
  components: {},
  computed: {},
  beforeMount() {},
  created(){
     this.query = this.$route.query
  },
  mounted() {
      window.scrollTo(100,0)
  },
  methods: {},
  watch: {},
}
</script>
<style scoped>

.line {
    width: 7.5rem;
    border-top: 1px solid rgb(229,229,229);
    margin-left: -0.2rem;
    margin-top: .3rem;
}
.demoImage {
    display: flex;
    justify-content: center;
}
.demoImage img {
    display: block;
    padding-top: .3rem;
    padding-bottom: .3rem;
}
.times{
    display: flex;
    align-items: center;
}
.times img {
    width: .2rem;
    height: .2rem;
    margin-right: .05rem;
    display: block;
}
.times span {
    color: #979797;
    font-size: .22rem;
    margin-right: .1rem;
}
.content{
    padding:.37rem .2rem 0 .2rem;
    font-family: 'PingFangMedium';
    color: black;
}
.bold{
    font-weight: 600;
}
.title{
    font-size: .32rem;
    text-align: left;
    margin-bottom: .3rem;
}
</style>
<style>
#app{
    background:white;
}
.fonts p {
    margin-bottom: .3rem;
}
.fonts {
    font-size: .3rem;
    text-align: left;
    padding-top: .3rem;
}
</style>


