<template>
  <div class="stockHistory">
    <div style="overflow: auto;-webkit-overflow-scrolling:touch;width:100%;height:100vh;">
      <iframe :src="iframeSrc" frameborder="0" height="100%" :scrolling='scrolling' style="width: 1px; min-width: 100%; *width: 100%;">
      </iframe>
    </div>
    <div class="backToHome" @click="toHome">
    </div>
  </div>
</template>

<script>
  export default {
    name: 'iframePop',
    data() {
      return {
        iframeSrc: '',
        scrolling:"",
      }
    },
    created() {
      //   console.log('6')
      console.log(this.$route)
      this.iframeSrc = this.$route.query.path
      if (navigator.userAgent.match(/iPad|iPhone/i)) {
        this.scrolling = "no";
      } else {
        this.scrolling = "yes";
      }
    },
    mounted() {
      
      if (this.$route.query.page == 'p3') {
        console.log("1", this.$route.query.id)
        var form1 = new FormData()
        form1.append("id", this.$route.query.id);
        var that = this;
        $.ajax({
          type: "POST",
          url: host + "/qi/page/index/getIntreportUrl",
          data: form1,
          mimeType: "text/html",
          processData: false,
          contentType: false,
          dataType: 'JSON',
          success: function (res) {
            console.log(res)
            that.iframeSrc = res.data.url
          },
          error: function (err) {
            console.log(err)
          }
        });
      }
    },
    methods: {
      toHome() {
        if (this.$route.query.page == 'p4') {
          this.$router.push({
            name: 'p4'
          })
          location.reload()

        } else if (this.$route.query.page == 'p3') {
          this.$router.push({
            name: 'p3'
          })

          location.reload()
        }

      }
    }

  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* .stockHistory {
  min-height: 100vh;
}
  iframe {
    width: 100%;
    height: 100vh;
  } */
  .backToHome {
    width: 1.36rem;
    height: 1.37rem;
    background: url('../assets/img/backToHome.png') no-repeat;
    background-size: cover;
    position: fixed;
    left: .26rem;
    bottom: .26rem;
    z-index: 9999;
  }

</style>



