<template>
  <div id="app">
    
      <router-view />
      <div class="tanChuangBack" v-show="tanchuangShow">
      <div class="tanChuang">
        <div class="tanChuangContent">
          {{tanchuangBref}}
        </div>
        <div class="tanchuangExit" @click="tanchuangExit">
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  function addClass(dom, cls) {
    var dom_clsName = dom.className;
    if (dom_clsName.indexOf(cls) === -1) {
      dom.className = dom_clsName == '' ? cls : dom_clsName + ' ' + cls;
    }
  }

  function removeClass(dom, cls) {
    var dom_clsName = dom.className;
    dom.className = dom_clsName.replace(cls, '').replace(/(^\s*) | (\s*$)/g, '');
  }
  export default {
    name: 'App',
    data() {
      return {

        tanchuangShow: false,
        tanchuangBref: '',
      }
    },

    methods: {

      tanchuangExit() {
        this.tanchuangShow = false;
      }
    },
    mounted() {
     // 
      var that = this;
      var form = new FormData();
      form.append("model", "get_products");
      $.ajax({
        type: "POST",
        url:  host + "/qi/page/index/index",
        data: form,
        mimeType: "text/html",
        processData:false,
        contentType: false,
        success: function (res) {
        console.log(res);
        let data = JSON.parse(res).data;
        console.log(JSON.parse(res))
        if(JSON.parse(res).code == 1002){
            console.log(that.tanchuangShow);
           that.tanchuangShow = true;
           that.tanchuangBref = JSON.parse(res).msg
        }
        
        that.$store.state.pid = data[0].pid
        },error: function(err){
          console.log("err",err)
        }
      });
    },
    created() {

    },
  }

</script>
<style>
  @font-face{
      font-family: 'PingFangMedium'; 
      src: url('./assets/font/PingFangMedium.eot');
      src:url('./assets/font/PingFangMedium.woff') format('woff'),
          url('./assets/font/PingFangMedium.ttf') format('truetype'),
          url('./assets/font/PingFangMedium.svg') format('svg');
  }
  #app {
    background: #f4f4f2;
  }

  * {
    box-sizing: border-box;
  }

  /* iframe {
  display: none !important;
} */
html,body{
  width:100%;
  /* min-height:100%; */
}
  body {
    max-width: 750px;
    margin: auto;
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
     height:100%;
  }

  img {
    display: block;
  }

  .tanChuang {
    width: 4.49rem;
    height: 3.33rem;
    background: url('./assets/img/tanchuang.png') no-repeat;
    background-size: cover;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%)
  }

  .tanChuangBack {
    z-index: 999999;
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    width: 100vw;
    background: rgba(0, 0, 0, 0.5)
  }

  .tanChuangContent {
    font-size: .28rem;
    color: #a09a9a;
    width: 100%;
    text-align: center;
    position: absolute;
    bottom: 1.2rem;

  }

  .tanchuangExit {
    position: absolute;
    right: 0;
    top: 0;
    width: 0.5rem;
    height: 0.5rem;
  }

  .listsContent{
    color: #6c6c6c;
    margin: .2rem 0;
  }
  
.none {
  display: none;
}
.block{
  display: block;
}
.TheEnd {
    font-size: 0.3rem;
    height: 0.5rem;
    line-height: 0.5rem;
        text-align: center;
    background: #f3f3f3;
}
</style>



