<template>
  <div>
    <img src="../assets/img/qihuobanner.png" alt="">
    <div class="bar">
      <div class="bars" :class="active[0]" @click="addActive(0)">
        
      </div>
      <div class="bars" :class="active[1]" @click="addActive(1)">
        
      </div>
      <div class="bars" :class="active[2]" @click="addActive(2)">
        
      </div>
    </div>
    <div class="content">
      <!--  -->
      <div class="lists" :class="show[0]">
        <div class="videos" >
          <div class="videoList" v-for="item in videos" :key="item.id" @click="toVideo(item.content)">
              <img :src="item.first_img" alt="">
              <div class="blackCover"></div>
              <div class="time" :class=" today == item.create_at.slice(5,10).split('-')[0]+''+item.create_at.slice(5,10).split('-')[1]+''?'redBG':''">{{item.create_at.slice(5,10).split('-')[0]+''+item.create_at.slice(5,10).split('-')[1]+''}}</div>
              <img class="playIcon" src="../assets/img/icon.png" alt="">
              <div class="videoBref">{{item.title}}</div>
          </div>
         
        </div>
      </div>
      <!--  -->
      <div class="lists" :class="show[1]">
        <div class="headTag">
          <span :class="headTagActive[index] == ''?'':'headTagActive'" v-for="(item,index) in tags" :key="item.id" @click="clickTag(index,item.name,item.id)">{{item.name}} </span>
          <!-- <span class="headTagActive"></span> <span></span> <span></span> <span></span> -->
        </div>
        <div class="grey">
        </div>
        <div class="videos">
         <div class="videoList" v-for="item in videos" :key="item.id" @click="toVideo(item.content)">
              <img :src="item.first_img" alt="">
              <div class="blackCover"></div>
              <div class="time" :class=" today == item.create_at.slice(5,10).split('-')[0]+''+item.create_at.slice(5,10).split('-')[1]+''?'redBG':''">{{item.create_at.slice(5,10).split('-')[0]+''+item.create_at.slice(5,10).split('-')[1]+''}}</div>
              <img class="playIcon" src="../assets/img/icon.png" alt="">
              <div class="videoBref">{{item.title}}</div>
          </div>
        </div>
      </div>
      <!--  -->
      <div class="lists" :class="show[2]" v-for="item in data" :key="item.id"
        @click="toMore(item.content,item.create_at,item.title)">
        <div class="title">
          <span class="icon"><img src="../assets/img/dujiaicon.png" alt=""><span class="iconInside"></span></span>
          {{item.title}}
        </div>
        <div class="listsContent" v-html='item.introduction'>
        </div>
        <div class="line"></div>
        <div class="times">
          <img src="../assets/img/riqiicon.png" alt="">
          <span>{{item.create_at.split(' ')[0].replace(new RegExp( '-' , "g" ),'.')}}</span>
          <img src="../assets/img/timeicon.png" alt="">
          <span>{{item.create_at.split(' ')[1].slice(0,5)}}</span>
        </div>
        <div class="grey"></div>
      </div>
    </div>
    <div class="TheEnd" :class="daoDiShow">
      ~
    </div>
  </div>
</template>
<script>
  export default {
    name: 'QiHuoXueYuan',
    data() {
      return {
        active: ['active', '', ''],
        show: ["block", "none", "none"],
        today: '',
        /*******  /*******/
        data: [],
        // index
        typeNum: 7,
        // 
        pageNumber: 1,
        daoDiShow: 'none',
        videos:[],
        redBG:'',
        tags:[],
        headTagActive:[],
        tagNum:1,
        blank:[],
      }
    },
    components: {},
    computed: {},
    beforeMount() {},
    mounted() {
      // this.addActive(1)
      this.getTags();
      this.checkDate()

      var that = this;
      let typeSess = Number(sessionStorage.getItem("QiHuoTypeSess"))
      if (sessionStorage.getItem("QiHuoTypeSess")) {
        that.addActive(typeSess)
      } else {
        this.getdata(this.typeNum,1,1)
      }
      window.addEventListener('scroll', this.onScroll);
    },
    methods: {
      clickTag(index,tagName,id){
        var that = this;
        this.daoDiShow = 'none';
        this.data = [];
        this.videos = [];
        this.pageNumber = 1;
        this.tagNum = index+1;
        let blank = [];
        for (let i = 0; i < that.headTagActive.length; i++) {
          blank.push('')
        }
        if(that.headTagActive[index] == ''){
           that.headTagActive = blank
         this.$set(that.headTagActive,index,tagName)
        } 
        this.getdata(this.typeNum,1,id)
        console.log(that.headTagActive)
      },
      checkDate(){
         var date = new Date();
        let month = '';
        let todayDate = ''
        if (date.getMonth() < 9) {
          month = '0' + (date.getMonth() + 1) + ''

        } else {
          month = (date.getMonth() + 1) + ''
        }
        if (date.getDate() < 10) {
          todayDate = '0' + date.getDate() + ''
        } else {
          todayDate = date.getDate() + ''
        }
        this.today = month + todayDate
      },
      addActive(index) {
        console.log(index)
        this.typeNum = index + 7;
        sessionStorage.setItem("QiHuoTypeSess", index);
        this.getdata(index + 7, 1,1);
        this.active = ['', '', ''];
        this.active[index] = 'active'
        this.show = ['none', 'none', 'none'];
        this.show[index] = 'block'
        this.daoDiShow = 'none';
        this.data = [];
        this.videos = [];
        this.pageNumber = 1;
      },
      getTags(){
        var that = this;
        this.axios.get(host+'/qi/page.news/tags', {
        })
        .then(function (response) {
          that.tags = response.data.msg
          for (let i = 0; i < that.tags.length; i++) {
            that.headTagActive .push('')
          }
          that.headTagActive[0] = '1';
        })
        .catch(function (error) {
          console.log(error)
        })
      },
      // 
      getdata(type, pageNum,tag) {
        var that = this;
        console.log(type, pageNum,tag)
        this.axios.get(host + '/qi/page.news/news?type=' + type+'&child_news_type='+tag, {
            params: {
              page: pageNum,
              size: 10
            }
          })
          .then(function (response) {
             var res = response.data.msg
             console.log(res)
            if(response.data.msg == "") {
            $(".tanChuangBack").show();
            $(".tanchuangExit").click(function () {
              $(".tanChuangBack").hide();
            })
          }
              if (res.length > 0) {
                for (let i = 0; i < res.length; i++) {
                  if (type == 9) {
                    that.data.push(res[i])
                  }else {
                    that.videos.push(res[i])
                  }
                  
                }
              } else {
                that.daoDiShow = 'block'
              }

          })
          .catch(function (error) {})
      },
      toMore(content, time, title) {
        console.log(content, time, title);
        this.$router.push({
          name: 'headLineContent',
          query: {
            content: content,
            time: time,
            title: title,
          }
        })
      },
      toVideo(url){
        window.location.href  = url
      },
      onScroll() {
        var that = this;
        //
        let innerHeight = document.querySelector('#app').clientHeight;
        //
        let outerHeight = document.documentElement.clientHeight;
        //
        let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        //scrollTop0innerHeight < (outerHeight + scrollTop)
        if (innerHeight <= (outerHeight + scrollTop)) {
          //
          console.log("")
          that.pageNumber++;
          that.getdata(that.typeNum, that.pageNumber,that.tagNum);
        }
      }
    },
    watch: {},
  }

</script>
<style scoped>
.redBG {
      background: #e54334 !important;
}
  .headTagActive {
    color: white;
    background: #ff324b !important;
  }

  .videoBref {
    height: .55rem;
    width: 100%;
    color: white;
    font-size: .22rem;
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
    background: rgba(0, 0, 0, .8);
    line-height: .55rem;

  }

  .playIcon {
    width: .6rem;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -.3rem;
    margin-top: -.3rem;
  }

  .videos {
    margin-top: .2rem;
    overflow: hidden;
    width: 7.3rem;
    /* position: absolute; */
  }

  .blackCover {
    width: 100%;
    height: 100%;
    background: black;
    opacity: 0.5;
    position: absolute;
  }

  .time {
    position: absolute;
    top: 0;
    right: 0;
    border-bottom-left-radius: 5px;
    width: 1.2rem;
    height: .35rem;
    background: #747373;
    color: white;
    font-size: .2rem;
    text-align: center;
    /* font-weight: 300; */
  }

  .timeActive {
    background: #ec5143;
  }

  .videoList {
    width: 3.45rem;
    height: 2.57rem;
    background: #606060;
    border-radius: 10px;
    float: left;
    margin-right: .2rem;
    margin-bottom: .2rem;
    display: flex;
    overflow: hidden;
    position: relative;
  }

  .content {
    font-family: 'PingFangMedium'
  }

  .headTag span {
    border-radius: 5px;
    padding: .1rem;
    background: #eaeaea;
    font-size: .24rem;
    margin-right: 0.1rem;
    margin-bottom: 0.22rem;
  }

  .headTag {
    width: 7.5rem;
    padding: .22rem .22rem 0 .22rem;
    margin-left: -.2rem;
  }

  .block {
    display: block;
  }

  .none {
    display: none;
  }

  .grey {
    height: .2rem;
    background: #f3f3f3;
    width: 7.5rem;
    margin-left: -.2rem;
  }

  .icon img {
    display: inline;
    width: 0.23rem;
    margin: 0 0.05rem;
    margin-bottom: 0.05rem;
  }

  .active {
    border-bottom: .04rem solid #fb004d;
    color: black;
    font-weight: 600;
  }

  .bar {
    display: flex;
    font-size: .3rem;
    font-family: 'PingFangMedium';
    color: #606060;
    background: white;
    border-bottom: 1px solid #e5e5e5;
  }

  .bars {
    width: 33%;
    height: .85rem;
    line-height: .85rem;
  }

  .title {
    padding-top: .2rem;
    align-items: center;
  }


  .times {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: .6rem;
  }

  .times img {
    width: .2rem;
    height: .2rem;
    margin-bottom: .05rem;
    margin-right: .05rem;
    display: block;
  }

  .times span {
    color: #979797;
    font-size: .22rem;
    margin-right: .1rem;
  }

  .line {
    border-bottom: 1px solid #f7f7f7;
    width: 7.5rem;
    margin-left: -.2rem;
  }

  .chaKan {
    margin: .4rem 0;
  }

  .listsContent p {
    color: #6c6c6c;
    margin: .2rem 0;
  }

  .content {
    color: black;
    background: white;
    text-align: left;
    font-family: 'PingFangMedium';
    padding-left: .2rem;
    padding-right: .2rem;
  }

  .lists {
    font-size: .32rem;
  }


  .icon {
    border-radius: 3px;
    color: white;
    font-size: .12rem;
    padding: 2px;
    font-family: Arial, Helvetica, sans-serif;
    background: #ff324b;
    margin-right: .1rem;
    
    font-size: 0.24rem;
    vertical-align: middle;
    width: fit-content;
  }

  .iconInside {
    border-radius: 3px;
    background: white;
    padding: 0 2px;
    margin-left: .02rem;
    color: #ff324b;
  }

</style>



