<template>
  <div class="p3">
    <div class="internalReport">
      <ul class="reportUl">
        <li class="reportList rel" v-for="(item,index) in reportArray" :key="index">
          <div class="reporImage">
            <img :src="item.first_img" alt="">
          </div>
          <div class="reportSection">
            <div class="reporTitle">{{item.title}}</div>
            <div class="protitle">{{item.protitle}}</div>
            <!-- <ul class="reportTitleUl">
              <li class="reportSStitle" v-for="(title,key) in item.characteristic" :key="key">
                <span class="SStitleIcon"></span>
                <span class="SStitleWords">{{title}}</span>
              </li>
            </ul> -->
            <div class="abs">

              <div class="lookMore" @click="iframeLink(item.id)">
                <span></span>
              </div>

            </div>


          </div>

        </li>
        <div class="daoDi" v-show="daoDiShow">
          ~
        </div>
      </ul>

    </div>
  </div>
</template>
<script>
  var pageIndex = 1;
  var isDaoDi = true;
  export default {
    name: 'p3',
    data() {
      return {
        reportArray: [],
        daoDiShow: false,
      }
    },
    components: {

    } ,
    activated() {
      window.removeEventListener('scroll', this.onScroll);
      isDaoDi = true;
      this.arrayClassOne = []
      //console.log('activated')
      this.loadList(this.pid);
      this.daoDiShow = false;
      pageIndex =1 ;
    },
    mounted(){
       this.loadList(this.pid);
    },
    computed: {
      pid: function () {
        return this.$store.state.pid
      }
    },
    watch: {
      pid(val) {
        isDaoDi = true;
        this.daoDiShow  = false;
        console.log(val)
        this.loadList(val)
      }
    },
    methods: {

      onScroll() {
        
        if (this.$route.name == 'p3') {
        var that = this;
        //
        let innerHeight = document.querySelector('#app').clientHeight;
        //
        let outerHeight = document.documentElement.clientHeight;
        //
        let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        //scrollTop0innerHeight < (outerHeight + scrollTop)
        // console.log(innerHeight + " " + outerHeight + " " + scrollTop);
        if (innerHeight == (outerHeight + scrollTop)) {

          //
          console.log("", isDaoDi)
            that.daoDiShow = true;
            pageIndex++; //
            var that = this;
            var form = new FormData();
            console.log('', pageIndex)
            form.append("model", "get_intreport");
            form.append("page", pageIndex);
            form.append("offset", 5);
            form.append("product_id", this.pid);
            $.ajax({
              type: "POST",
              url: host + "/qi/page/index/index",
              data: form,
              mimeType: "text/html",
              processData: false,
              contentType: false,
              dataType: 'json',
              success: function (res) {
                console.log(res)
                res = res.data
                if (res.length == 0) {
                  isDaoDi = false;
                  that.daoDiShow = true;
                } else {
                  isDaoDi = true;
                  if (res.length > 0) {
                    for (let i = 0; i < res.length; i++) {
                      that.reportArray.push(res[i])
                    }
                  }
                  console.log(res.length)
                  //offset
                  if (res.length < 5) {
                    that.daoDiShow = true;
                  }
                }


              }
            });
            this.items += 10;

          
        }
      
      }
      },
      loadList(val) {
        if (val !== '') {
          this.reportArray = [];
          pageIndex = 1;
          // var loading = this.$loading({
          //   lock: true,
          //   text: '',
          //   spinner: 'el-icon-loading',
          //   background: 'rgba(255, 255, 2550.3)'
          // });
          console.log("loadmore");
          var that = this;
          var form = new FormData();
          form.append("model", "get_intreport");
          form.append("page", pageIndex);
          form.append("offset", 5);
          form.append("product_id", val);

          $.ajax({
            type: "POST",
            url: host + "/qi/page/index/index",
            data: form,
            mimeType: "text/html",
            processData: false,
            contentType: false,
            dataType: 'json',
            success: function (res) {
              console.log(res)
              res = res.data
              that.reportArray = res
              window.addEventListener('scroll', that.onScroll);
            }
          });


        }
      },
      iframeLink(id) {
        console.log(id);
        this.$router.push({
          name: 'iframePop',
          query: {
            page: 'p3',
            id: id,
          }
        })
      },
    }
  }

</script>
<style scoped>
  .rel {
    position: relative;
  }

  .abs {
    position: absolute;
    right: .4rem;
    bottom: .3rem;
    width: 100%;
  }

  .internalReport {
    font-size: 0.2rem;
  }

  .internalReport {
    background-color: #f4f4f3;
  }

  .reportUl {
    padding: 0.16rem 0 0.08rem;
    background-color: #f5f5f4;
  }

  .reportList {
    padding: .26rem 0 .3rem .3rem;
    background-color: #fff;
    overflow: hidden;
    margin-bottom: 0.16rem;
  }

  .reporImage {
    float: left;
    width: 2.26rem;
    height: 2.95rem;
    overflow: hidden;
    margin-right: .5rem;
  }

  .reporImage>img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .reportSection {
    float: left;
    width: 4rem;
    margin-top: .25rem;
  }

  .reporTitle {
    font-size: .34rem;
    color: #47443e;
    line-height: .46rem;
    margin-bottom: .2rem;
    font-weight: bold;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }

  .protitle {
    font-size: .3rem;
    color: #47443e;
    line-height: .4rem;
    text-align: left;
    overflow: hidden;
    margin-bottom: 0.09rem;
    border-top: 1px solid #d2d2d2;
    padding-top: .2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }

  .reportTitleUl {
    overflow: hidden;
    padding-top: .2rem;
    padding-bottom: .18rem;
    border-top: .1px solid #d2d2d2;
  }

  .reportTitleUl>li {
    overflow: hidden;
    float: left;
    width: 49%;
    margin-bottom: .14rem;
  }

  .SStitleIcon {
    float: left;
    width: .08rem;
    height: .08rem;
    background-color: #7d7d7d;
    border-radius: 0.04rem;
    margin-top: 0.09rem;
    margin-right: .1rem;

  }

  .SStitleWords {
    float: left;
    font-size: .26rem;
    line-height: 0.26rem;
    height: 0.26rem;
    color: #989898;
    white-space: nowrap;
    width: 80%;
    text-align: left;
  }

  .lookMore {
    /* position: relative;
    left: 0;
    top: 0;
    z-index: 0;
    height: .5rem; */
  }

  .lookMore>span {
position: absolute;
    right: 0;
    top: -0.4rem;
    z-index: 0;
    padding: 0.05rem .24rem;
    border-radius: 0.24rem;
    /* height: 100%; */
    background-color: #0f3d8f;
    font-size: .26rem;
    /* font-weight: bold; */
    color: #fff;
    cursor: pointer;
  }

</style>



