<template>
  <div id="mainPage">
    <div class='back'>
      <swiper :options="swiperOption" ref="mySwiper">
        <!-- slides -->
        <swiper-slide v-for="(slide,index) in swiperSlides" v-if="arr.indexOf(arr[index]) != -1" :key="index"
          :data_name="slide.pid">
          <img class="vipCard" :src="slide.product_img" alt="">
        </swiper-slide>

      </swiper>
      <!-- <img class="vipCard" src="@/assets/img/vipcard.png" alt=""> -->
      <img class="white" src="@/assets/img/white1.png" alt="">
      <div class="nav">
        <div>
          <router-link to="/">
            <div class="black"></div>
            <img :src="src1" alt="">
          </router-link>
        </div>
        <div>
          <router-link to="/tuoshui">
            <div class="black"></div>
            <img :src="src2" alt="">
          </router-link>
        </div>
        <div>
          <router-link to="/DragonTiger">
            <div class="black"></div>
            <img :src="src3" alt="">
          </router-link>
        </div>
      </div>
      <div class="nav1">
        <div>
          <router-link to="/TouZiNeiCan">
            <div class="black"></div>
            <img :src="src4" alt="">
          </router-link>
        </div>
        <div>
          <router-link to="/QiHuoXueYuan">
            <div class="black"></div>
            <img :src="src5" alt="">
          </router-link>
        </div>
        <div>
          <router-link to="/TaoLiDaShi">
            <div class="black"></div>
            <img :src="src6" alt="">
          </router-link>
        </div>
      </div>

    </div>
    <div class="headline" @click="toHeadLine">
      <img src="../assets/img/headlineIcon.png" alt="">
      <div>
        <p v-for="item in headline" :key="item.id">{{item.title}}</p>
      </div>
    </div>
    <!-- <transition :name="global.transitionName"> -->
    <keep-alive>
      <router-view />
    </keep-alive>
    <!-- </transition> -->

  </div>

</template>

<script>
  export default {
    name: 'mainPage',
    data() {
      return {
        arr: [],
        style1: 'color:#fb5545!important',
        style2: 'color:black',
        style3: 'color:black',
        style4: 'color:black',
        src1: require('@/assets/img/celvuecankao.png'),
        src2: require('@/assets/img/tuoshuiyanbao.png'),
        src3: require('@/assets/img/longhubang.png'),
        src4: require('@/assets/img/touzineican.png'),
        src5: require('@/assets/img/qihuoxueyuan.png'),
        src6: require('@/assets/img/taolidashi.png'),
        actIndex: '',
        swiperOption: {
          pagination: {
            el: '.swiper-pagination'
          },

        },
        swiperSlides: [],
        headline:[],
      }
    },
    created() {
      let p1 = [{
          ll: '1,2,6'
        },
        {
          ll: '2,1,6'
        }

      ]
      let o1 = p1.filter((item) => {
        return item.ll.indexOf('1') >= 0
      })
      // console.log(o1)
    },
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    },
    methods: {
      toHeadLine(){
        this.$router.push({
          name: 'headLine'
        })
      }
    },
    mounted() {
      // 
      this.axios.get(host + '/qi/page.news/news?type=13' )
      .then(function (response) {
        for (let i = 0; i < 2; i++) {
          that.headline.push(response.data.msg[i])
        }
      })
      .catch(function (error) {
      })
      var that = this;
      var form = new FormData();
      form.append("model", "get_products");
      $.ajax({
        type: "POST",
        url: host + "/qi/page/index/index",
        data: form,
        mimeType: "text/html",
        processData: false,
        contentType: false,
        success: function (res) {
          // console.log("", JSON.parse(res))
          let data = JSON.parse(res).data;
          that.swiperSlides = data;
          // pid
          //  console.log(data)
          for (let i = 0; i < data.length; i++) {
            that.arr.push(data[i].pid);
          }

          // console.log(that.arr)
          that.$store.state.pid = that.arr[0]
          // console.log("pid", that.$store.state.pid)
        },
        error: function (err) {
          console.error(err);

        }



      });


      // 

      //  console.log('this is current swiper instance object', this.swiper)
      this.swiper.on('slideChangeTransitionEnd', function (e) {
        that.$store.state.pid = $(".swiper-slide-active").attr("data_name")
        console.log('pid', that.$store.state.pid)
      })


      // console.log( this.swiper.activeIndex)





      // 
      // this.$router.beforeEach((to, from, next) => {
      //   // console.log(to)
      //   window.scroll = 0;
      //     if(to.name == 'p1') {
      //         // this.toPage1();
      //         next()
      //     }
      //   else if(to.name == 'p2') {
      //         // this.toPage2();
      //         next()
      //     } else if (to.name == 'p3'){
      //         // this.toPage3();

      //         next()

      //     } else if (to.name == 'p4'){
      //         // this.toPage4();

      //         next()

      //     } else {
      //       next();
      //     }
      // })

      //  console.log(this.$route.name)
      if (this.$route.name == 'p2') {
        this.toPage2();
      } else if (this.$route.name == 'p3') {
        this.toPage3();
      } else if (this.$route.name == 'p4') {
        this.toPage4();
      }

    }
  }

</script>

<style>
  .headline {
    height: 1.06rem;
    border-top: 3px solid #f5f5f4;
    background: #fff;
    display: flex;
    align-items: center;
  }

  .headline img {
    margin-left: .19rem;
    margin-right: .19rem;
    width: .7rem;
    height: .7rem;
  }

  .headline div p {
    font-size: .24rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    word-break: break-all;
    width: 6.1rem;
    text-align: left;
  }

  .swiper-container {
    z-index: 0 !important;
  }

  #app {
    background: #f4f4f2;
  }

  * {
    box-sizing: border-box;
  }

  /* iframe {
  display: none !important;
} */
  body {
    max-width: 750px;
    margin: auto;
  }

  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;

  }

  img {
    display: block;
  }

  .back {
    width: 100%;
    margin: auto;
    position: relative;
    left: 0;
    top: 0;
    z-index: 0;
    height: 6.6rem;
    background: url('../assets/img/back.png') no-repeat center top;
    background-size: cover;
  }

  .nav,
  .nav1 {
    position: absolute;
    left: 0;
    top: 3.3rem;
    display: flex;
    /* justify-content: center; */
    width: 100%;
  }

  .nav img,
  .nav1 img {
    margin-left: .19rem;
    display: block;
    width: 2.23rem;
    height: 1.68rem;
  }

  .black {
    width: 2.23rem;
    height: 1.46rem;
    background: rgba(0, 0, 0, .5);
    position: absolute;
    left: 0;
    top: 0;
    margin-left: .19rem;
    border-radius: 5px;
  }

  .nav1 {
    top: 4.9rem;
  }

  .nav li {
    font-size: .31rem;
  }

  .nav li a {
    color: #47443e !important;
    font-weight: 500;
    display: block;
  }

  .nav li img {
    margin-left: .43rem;
    margin-right: .43rem;
    width: 2.24rem;
    height: auto;
    margin-bottom: .15rem;
  }

  .vipCard {
    display: block;
    width: 6.9rem;
    padding-top: .5rem;
    margin: auto;
    max-height: 4rem;
  }

  .white {
    position: absolute;
    bottom: -0.07rem;
    left: 0;
    z-index: 0;
    width: 100%;
    height: auto;
    pointer-events: none;
  }

  .el-loading-spinner {
    display: flex !important;
    justify-content: center !important;
    text-align: center !important;

  }


  .el-loading-spinner i {
    margin-top: 0 !important;
    font-size: 0.7rem !important;
    color: #2f354f !important;
    transform-origin: 50% 50% !important;
    display: inline-flex !important;
  }

  .daoDi {
    font-size: 0.3rem;
    height: 0.5rem;
    line-height: 0.5rem;
  }

  .p1,
  .p2,
  .p3,
  .p4 {
    min-height: calc(100vh - 6.6rem);
  }

  .router-link-exact-active .black {
    background: unset;
  }

  .nav a,
  .nav1 a {
    position: relative;
    display: block;
  }

</style>



