<template>
  <div class="stockHistory">
    <ul class="stockHistorySection">
      <!--  -->
      <li v-for="item in arrs" class="stockHistoryList" :key="item.id">
        <div>
          <div class="itemLists">
            <img class="heyuename" src="../assets/img/heyuename.png" alt="">
            <span>:{{item.stock_name}}</span>
          </div>
          <div class="itemLists">
            <img class="heyuename" src="../assets/img/maimaifangxiang.png" alt="">
            <span class="maiMaiFangXiangImg">:
              <img src="../assets/img/mairuzuoduo1.png" v-if="item.operation_type == 1">
              <img src="../assets/img/maichuzuokong1.png" v-if="item.operation_type == 2">
              <img src="../assets/img/pingcangzhiying1.png" v-if="item.operation_type == 3">
              <img src="../assets/img/pingcangzhisun1.png" v-if="item.operation_type == 4">
              <!--  -->
              <img src="../assets/img/jixuchiyou1.png" v-if="item.operation_type == 5">
            </span>
          </div>
          <div class="itemLists">
            <img class="heyuename" src="../assets/img/jianyicangwei.png" alt="">
            <span>: {{item.position}}</span>
          </div>
          <div class="itemLists">
            <img class="heyuename" src="../assets/img/jianyijiage.png" alt="">
            <span>: {{item.buy_price}}</span>
          </div>
          <div class="itemLists">
            <img class="heyuename" src="../assets/img/jianyizhiying.png" alt="">
            <span>: {{item.profit}}</span>
          </div>
          <div class="itemLists">
            <img class="heyuename" src="../assets/img/jianyizhisun.png" alt="">
            <span>: {{item.loss}} </span>
          </div>
          <div class="itemLists">
            <img class="heyuename" src="../assets/img/caozuobeizhu.png" alt="">
            <span>: {{item.notice}} </span>
          </div>
        </div>

        <div class="timeIcons">
          <img src="../assets/img/riqiicon.png" alt="">
          <span>{{item.date}}</span>
          <img src="../assets/img/timeicon.png" alt="">
          <span>{{item.time}}</span>
        </div>
      </li>

    </ul>
    <div class="stockHistoryFoot" v-show="bottomShow">
      <div class="footHead"><span></span></div>
      <div class="footProgram"></div>
      <div class="footProgram"></div>
    </div>
    <div class="backToHome" @click="toHome">
    </div>

  </div>
</template>

<script>
  export default {
    name: 'futuresHistory',
    data() {
      return {
        arrs: [],
        // arrs: [{
        //   stock_name: 1,
        //   operation_type: 5,
        //   position:1,
        //   buy_price:1,
        //   profit:1,
        //   loss:1,
        // }],
        bottomShow: true,
      }
    },
    created() {
      //   console.log('6')

    },
    mounted() {
      var form1 = new FormData();
      form1.append("model", "get_strategy_detail");
      console.log(this.$store.state.pid)
      form1.append("pid", this.$route.query.pid);


      var that = this;
      $.ajax({
        type: "POST",
        url: host + "/qi/page/index/index",
        data: form1,
        mimeType: "text/html",
        processData: false,
        contentType: false,
        dataType: 'JSON',
        success: function (res) {
          console.log(res)
          that.bottomShow = true;
          that.arrs = res.data
          let i = 0;
          var innerHeight = 0;
          gundong(i)

          function gundong(i) {
            //
            let outerHeight = document.documentElement.clientHeight;
            //
            let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
            // console.log(innerHeight,outerHeight,scrollTop)
            if (innerHeight == (outerHeight + scrollTop)) {
              clearTimeout(c);
              console.log('')
            } else {
              var c = setTimeout(function () {
                innerHeight = document.querySelector('#app').clientHeight;
                i = i + innerHeight / 100;
                gundong(i);
                window.scroll(100, i);
              }, 10)
            }
          }

        },
        error: function (err) {
          console.error(err)
        }
      });
    },
    methods: {
      toHome() {
        this.$router.replace({
          name: 'p1'
        })

        location.reload();
      },

    }

  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .itemLists {
    display: flex;
    align-items: center;
    height: 1rem;
    border-bottom: 1px solid #efefef;
    margin: 0 .45rem;
  }

  .itemLists:last-child {
    border-bottom: 0;
  }

  .heyuename {
    width: .3rem;
    display: inline;
    margin-right: .2rem;
  }

  .timeIcons img {
    width: .2rem;
    height: .2rem;
    display: inline;
    margin-left: .2rem;
  }

  .timeIcons span {
    color: #cacaca;
    margin-left: .1rem;
  }

  .timeIcons {
    height: .7rem;
    display: flex;
    align-items: center;
    font-size: .25rem;
    border-top: 1px solid #efefef;

  }

  .stockHistory {
    font-size: 0.2rem;
    padding: .24rem 0 0;
    background-color: #f3f3f3;

    min-height: 100vh;
  }

  .stockHistoryList {
    background-color: #fff;
    margin-bottom: 0.14rem;
    font-size: .3rem;
  }

  .stockcreate_at {
    height: .75rem;
    line-height: .75rem;
    padding-left: .24rem;
    border-bottom: 1px solid #f3f3f3;
    font-size: .24rem;
    color: #999999;
    text-align: left;
  }



  .stockHistoryContain {
    width: 7rem;
    margin: auto;
    padding: .35rem 0 .35rem;
    font-size: .3rem;
    color: #333333;
    line-height: .44rem;
    text-align: left;
  }

  .stockHistoryFoot {
    padding: .4rem 0 0.3rem;
    background-color: #fff;
  }

  .footHead {
    width: 5.12rem;
    margin: 0 auto 0.3rem;
    height: 1px;
    background-color: #e9e9ec;
    position: relative;
    left: 0;
    top: 0;
    z-index: 0;

  }

  .footHead>span {
    position: absolute;
    left: 50%;
    top: -0.15rem;
    z-index: 0;
    width: .8rem;
    margin-left: -0.4rem;
    height: .3rem;
    line-height: .3rem;
    text-align: center;
    letter-spacing: .06rem;
    font-size: .25rem;
    color: #a0a0a0;
    background-color: #fff;

  }

  .footProgram {
    font-size: .2rem;
    color: #646464;
    line-height: 1.5;
    padding: 0 .6rem;
  }

  .footTeacher {
    overflow: hidden;
    margin-top: .26rem;
    width: fit-content;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
  }

  .footTeachericon {
    float: left;
    text-align: center;
    line-height: 1.2;
    padding: .2rem;
    border-radius: .06rem;
    background-color: #9c9c9c;
    font-size: .24rem;
    color: #fff;
    margin-right: .24rem;
  }

  .footTeacherList {
    float: left;
  }

  .footTeacherList>p {
    font-size: .2rem;
    color: #6b6b6b;
    line-height: 1.6;
    text-align: left;
  }

  .backToHome {
    width: 1.36rem;
    height: 1.37rem;
    background: url('../assets/img/backToHome.png') no-repeat;
    background-size: cover;
    position: fixed;
    left: .26rem;
    bottom: .26rem;
    z-index: 9999;
  }
  .maiMaiFangXiangImg img{
    height: .32rem;
    display: inline;
  }
  .itemLists:last-child {
    height: auto;
    min-height: 1rem;
  }
</style>



