<template>
  <div>
    <img src="../assets/img/touzibanner.png" alt="">
    <div class="bar">
      <div class="bars" :class="active[0]" @click="addActive(0)">
        <img src="../assets/img/daihuo.png" alt="">
      </div>
      <div class="bars" :class="active[1]" @click="addActive(1)">
        
      </div>
    </div>
    <div class="content">
        <!--  -->
        <div :class="list[0]">
             <div class="lists" v-for="item in datas" :key="item.id" @click="toVideo(item.url,item.id)">
               <div class="listLeft">
                   <img :src="item.first_img" alt="">
               </div>
               <div class="listRight">
                   <div class="listTitle">{{item.title}}</div>
                   <div class="listContent">
                       <div class="teacherName">
                       </div>
                       <div class="playNum">
                           <img src="../assets/img/playlittleicon.png" alt="">
                           <span>{{item.viewtime}}</span>
                       </div>
                   </div>
               </div>
           </div>
           
        </div>
        <!--  -->
        <div :class="list[1]">
            <div class="todays" v-for="item in celveDatas" :key="item.id" @click="toCelve(item.file_url,item.id)">
                 <div class="todayleft">
                     <img :src="item.first_img" alt="">
                 </div>
                 <div class="todayright">
                     <div class="rightTitle">{{item.title}}</div>
                     <div class="rightLine"></div>
                     <div class="rightBref">
                         {{item.protitle}}
                     </div>
                     <img class="startReading" src="../assets/img/startReading.png" alt="">
                 </div>
            </div>
        </div>         
    </div>
    
      <div class="TheEnd" :class="show">
        ~
      </div>
  </div>
</template>
<script>
  export default {
    name: 'TouZiNeiCan',
    data() {
      return {
        active: ['active', ''],
        list:['block','none'],
        datas:[],
        celveDatas:[],
        pageNumber:1,
        celvePageNumber:1,
        show:'none',
        index:0,
      }
    },
    components: {},
    computed: {},
    beforeMount() {},
    computed: {
      pid: function () {
        return this.$store.state.pid
      }
    },
    watch: {
      pid(val) {
           var that = this;
           let typeSess = Number(sessionStorage.getItem("DragonTigerTypeSess"))
            if(sessionStorage.getItem("DragonTigerTypeSess")){
        that.addActive(typeSess)
      } else {
          this.getVideos(val,that.pageNumber)
      }
        
      }
    },
    mounted(){
        var that = this;
        if(this.$store.state.pid){
            that.getVideos(this.$store.state.pid,that.pageNumber)
            that.getCeLve(this.$store.state.pid,that.celvePageNumber)
        }
        
     
        window.addEventListener('scroll', this.onScroll);
    },
    methods: {
        // 
      getVideos(val,pageNumber){
        var that = this;
          var form = new FormData();
          form.append("model", "get_video");
          form.append("page", pageNumber);
          form.append("offset", 10);
          form.append("product_id", val);
          form.append("type", 0);
        this.axios.post(host + "/qi/page/index/index",form)
        .then(function (response) {
          if(response.data.msg == "") {
            $(".tanChuangBack").show();
            $(".tanchuangExit").click(function () {
              $(".tanChuangBack").hide();
            })
          }
            // console.log(response.data.data)
            let data = response.data.data
            if(data.length>0){
                    for (let i = 0; i < data.length; i++) {
                that.datas.push(data[i])
            }
            } else {
                that.show = 'block'
            }
            
        })
        .catch(function (error) {
            console.log(error)
        })
      },
        //   
        getCeLve(val,pageNumber){
            var that = this;
            var form = new FormData();
            form.append("model", "get_intreport");
            form.append("page", pageNumber);
            form.append("offset", 10);
            form.append("product_id", val);
            this.axios.post(host + "/qi/page/index/index",form)
            .then(function (response) {
                console.log(response.data.data)
               if(response.data.msg == "") {
            $(".tanChuangBack").show();
            $(".tanchuangExit").click(function () {
              $(".tanChuangBack").hide();
            })
          }
                let data = response.data.data
                if(data.length>0){
                        for (let i = 0; i < data.length; i++) {
                    that.celveDatas.push(data[i])
                }
                } else {
                    that.show = 'block'
                }
                
            })
            .catch(function (error) {
                console.log(error)
            })
        },
       onScroll() {
          var that = this;
          //
          let innerHeight = document.querySelector('#app').clientHeight;
          //
          let outerHeight = document.documentElement.clientHeight;
          //
          let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
          //scrollTop0innerHeight < (outerHeight + scrollTop)
          if (innerHeight <= (outerHeight + scrollTop)) {
            //
            console.log("")
            if(that.index == 0){
                that.pageNumber++;
                that.getVideos(this.$store.state.pid,that.pageNumber);
            } else {
                that.celvePageNumber++;
                that.getCeLve(this.$store.state.pid,that.celvePageNumber);
            }
          }
      },
      addActive(index) {
           sessionStorage.setItem("DragonTigerTypeSess", index);
          this.index = index;
        this.active = ['', ''];
        
        this.list = ['none', 'none'];
        this.active[index] = 'active';
        this.list[index] = 'block';
        this.datas = [];
        this.pageNumber = 1;
        this.celveDatas = [];
        this.celvePageNumber = 1;
         this.show = 'none';
        if(index == 0){
            this.getVideos(this.$store.state.pid,1)
        } else {
            this.getCeLve(this.$store.state.pid,1)
        }

      },
      toCelve(url,id){
        // 
        var form = new FormData();
        form.append("id", id);
        this.axios.post(host + "/qi/page/index/getIntreportUrl",form)
        .then(function (response) {
            window.location.href  = url
        })
        .catch(function (error) {
            console.log(error)
        })
      },
      toVideo(url,id){
        // 
        var form = new FormData();
        form.append("id", id);
        this.axios.post(host + "/qi/page/index/getVideoUrl",form)
        .then(function (response) {
           window.location.href  = url
        })
        .catch(function (error) {
            console.log(error)
        })
          
      }
    }
  }

</script>
<style scoped>

.none {
  display: none;
}
.block{
  display: block;
}
.TheEnd {
    font-size: 0.3rem;
    height: 0.5rem;
    line-height: 0.5rem;
        text-align: center;
    background: #f3f3f3;
}
.startReading {
    width: 1.38rem;
    height: .43rem;
        margin-top: .4rem;
    float: right;
}
.rightBref {
    font-size: .28rem;
        width: 3.75rem;
    color: #717171;
    text-align: left;
}
.rightLine {
    border-bottom: 1px solid #d5d5d5;
    width: 3.75rem;
    margin: .25rem 0 ;
}
.todayright {
    margin-left: .3rem;
}
.rightTitle {
    font-size: .34rem;
    color: black;
    font-weight: 600;
    text-align: left;
}
.content {
    font-family: 'PingFangMedium';
}
.todayleft {
    width: 2.28rem;
    height: 2.97rem;
    display: flex;
    align-items: center;
    background: #606060;
}
.todayleft img {
    width: 100%;
}
.todays {
    width: 7.5rem;
    padding:.25rem;
    border-bottom: 1px solid #dcdcdc;
    display: flex;
    background: white;
    justify-content: space-around;
}
.none {
    display: none;
}
.block {
    display: block;
}
.listTitle {
    text-align: left;
    margin-bottom: .1rem;
}
.playNum img {
    display: inline;
    width: .22rem;
    height: .22rem;
}
.listContent {
font-size: .26rem;
color: #999999;
display: flex;
justify-content: space-between;
}
.listRight {

    color: black;
    font-size: .32rem;
    height: 1.5rem;
    margin-left: .3rem;
    width: 4.8rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.listLeft img {
    width: 100%;
}
.listLeft {
    background: #606060;
    width: 2.2rem;
    height: 1.5rem;
        border-radius: 5px;
        overflow: hidden;
        display: flex;
        align-items: center;
}
.lists {
    width: 7.5rem;
    padding:.25rem;
    border-bottom: 1px solid #dcdcdc;
    display: flex;
    background: white;
    /* justify-content: space-between; */
}
    .bars img{
        display: inline;
        width: .2rem;
        height: .22rem;
        margin-left: .1rem;
    }
  .active {
    border-bottom: .04rem solid #fb004d;
    color: black;
    font-weight: 600;
  }

  .bar {
    display: flex;
    font-size: .3rem;
    font-family: 'PingFangMedium';
    color: #606060;
    background: white;
    border-bottom: 1px solid #e5e5e5;
  }

  .bars {
    width: 50%;
    height: .85rem;
    line-height: .85rem;
  }

</style>



